import './Css/InfoPage.scss'
import photoPerfil from '../Assets/Img/fotoPerfil.jpg'

export function InfoPage() {
    return <div className="infoScreen">
        <div className="info">
            <div className="nameContainer">
                <img src={photoPerfil} alt="BenjaminPhoto"/>
                <div className="infoName">
                    <h1>
                        Benjamín Garrido González
                        <hr/>
                    </h1>
                    <h2>
                        Desarrollador jr.
                    </h2>
                </div>
            </div>

            <br/>
            <p>
                Soy un programador de 25 años, autodidacta apasionado por la tecnología, la programación, el diseño y la
                experiencia de usuario. Me interesa formar parte del sector tecnológico como desarrollador de software
                con
                el fin de poner a prueba mis habilidades obtenidas y desarrollar nuevas en base a la experiencia.
            </p>
            <div className='curriculumLink'>
                <a  href="https://dyxbenjamin.github.io/portafolio/docs/Curriculum_BenjaminGarrido.pdf" target='_blank' rel="noreferrer">Descargar curriculum</a>
            </div>
        </div>
    </div>
}